
/* Style the tab */

.tab {
    overflow: hidden;
    border: 0px solid #ccc;
}

/* Style the buttons inside the tab */

.tab button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 30px 36px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    font-size: 17px;
}

/* Change background color of buttons on hover */

.tab button:hover {
    background-color: rgb(64, 113, 247);
}

/* Create an active/current tablink class */

.tab button.active {
    background-color: rgb(255, 255, 255);
}

/* Style the tab content */

.tabcontent {
    display: none;
    padding: 6px 12px;
    border: 1px solid rgb(255, 255, 255);
    border-top: none;
}


/* --------- */
.md_tabs {
    overflow: hidden;
    border: 0px solid #ccc;
}

/* Style the buttons inside the tab */

.md_tabs button {
    background-color: inherit;
    float: right;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 10px 16px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    font-size: 15px;
}

/* Change background color of buttons on hover */

.md_tabs button:hover {
    background-color: #ddd;
}

/* Create an active/current tablink class */

.md_tabs button.active {
    background-color: rgb(39, 166, 240);
}

/* Style the tab content */

.md_tabscontent {
    display: none;
    padding: 6px 12px;
    border: 1px solid rgb(255, 255, 255);
    border-top: none;
}


