.ranking_card {
    /* Add shadows to create the "card" effect */
    -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
            box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

/* On mouse-over, add a deeper shadow */
.ranking_card:hover {
    -webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
            box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

/* Add some padding inside the card container */
.ranking_container {
    padding: 10px 16px;
    border-spacing: 5px; 
    border-bottom: groove;
    border-color: white;
    background: -webkit-gradient(linear, left top, left bottom, from(#19aa89), to(#ccffcc));
    background: -webkit-linear-gradient(top, #19aa89 0%, #ccffcc 100%);
    background: -o-linear-gradient(top, #19aa89 0%, #ccffcc 100%);
    background: linear-gradient(to bottom, #19aa89 0%, #ccffcc 100%);
    -ms-flex-align: center;
        align-items: center;
    
}

.ranking_container_grey {
    padding: 10px 16px;
    border-spacing: 5px; 
    border-bottom: groove;
    border-color: white;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(54%, #999999), color-stop(99%, #ffffff));
    background: -webkit-linear-gradient(top, #999999 54%, #ffffff 99%);
    background: -o-linear-gradient(top, #999999 54%, #ffffff 99%);
    background: linear-gradient(to bottom, #999999 54%, #ffffff 99%);
    -ms-flex-align: center;
        align-items: center;
    
}

.ranking_container h1,h2,h3 {
    display: inline;
}

.ranking_container_grey h1,h2,h3 {
    display: inline;
}




